import { createPortal } from 'react-dom';
import { Formik } from 'formik';
import { Button } from '@mui/material';
import { navigationList } from 'data/navListData';
import FieldComponent from './FieldComponent';
import { useDispatch, useSelector } from 'react-redux';
import FieldAddImages from './FieldAddImages';
import Label from './Label';
import MultipleSelectColor from './MultipleSelectColor';
import MultipleSelectCategory from './MultipleSelectCategory';
import MultipleSelectSubCategory from './MultipleSelectSubCategory';
import MultipleSelectSex from './MultipleSelectSex';
import MultipleSelectState from './MultipleSelectState';
import MultipleSelectSize from './MultipleSelectSize';
import PriceComponent from './PriceComponent';
import FieldsCheckboxes from './FieldsCheckboxes';
import { createProduct } from '../../redux/product/thunk';
import {
  Box,
  Buttons,
  ContainerAddProduct,
  Explainment,
  FieldImagesList,
  Form,
  SelectorsList,
  addProductButton,
  viewProductButton,
} from './AddProductComponent.styled';
import {
  pictures,
  sizeFootwear,
  sizeClothes,
  colorProduct,
} from 'data/forAddProductPage';
import { toggleModalView } from '../../redux/modalViewProduct/slice';
import ViewAheadComponent from 'components/ViewAhead/ViewAheadComponent';
import { selectorViewAddingProductModal } from '../../redux/modalViewProduct/selectors';

const modalEnter = document.querySelector('#modal');

export default function AddProductComponent() {
  const dispatch = useDispatch();
  const onViewProduct = useSelector(selectorViewAddingProductModal);
  function aheadViewProduct() {
    dispatch(toggleModalView(true));
  }
  function handleSubmit(values) {
    const formData = new FormData();
    for (const key in values) {
      if (!values.hasOwnProperty(key)) return;
      if (key === 'file') {
        values[key].forEach(file => formData.append('file', file));
      } else {
        formData.append(key, values[key]);
      }
    }

    dispatch(createProduct(formData));
  }

  return (
    <ContainerAddProduct>
      <Formik
        initialValues={{
          title: '',
          price: 0,
          discount: false,
          eco: false,
          discountPrice: 0,
          category: '',
          subCategory: '',
          state: '',
          size: [],
          color: [],
          describe: '',
          sex: '',
          brand: '',
          isUkraine: false,
          file: [],
        }}
        onSubmit={values => {
          console.log(values);
          handleSubmit(values);
        }}
      >
        {({
          setFieldValue,
          values,
          handleChange,
          setSubmitting,
          isSubmitting,
        }) => (
          <Form>
            <Box>
              <FieldComponent
                name="title"
                required={true}
                type="text"
                label=""
                handleChange={handleChange}
                setSubmitting={setSubmitting}
                className="title"
                explainment="    3   100 "
                placeholder="  , :     , 50 "
              />
              <FieldComponent
                required={true}
                as="textarea"
                name="describe"
                className="describe"
                type="text"
                label=" "
                handleChange={handleChange}
                setSubmitting={setSubmitting}
                explainment="    30  4000 "
                placeholder="   , :          "
              />
              <FieldComponent
                name="brand"
                className="brand"
                type="text"
                label=""
                handleChange={handleChange}
                setSubmitting={setSubmitting}
                explainment="     50 "
                placeholder="  "
              />
            </Box>
            <Box>
              <SelectorsList>
                <li>
                  <label>
                    <Label label="" />
                    <MultipleSelectCategory
                      handleChange={handleChange}
                      setSubmitting={setSubmitting}
                      names={navigationList}
                      name="category"
                    />
                  </label>
                </li>
                {values.category !== '/' && (
                  <li>
                    <label>
                      <Label label="" />
                      <MultipleSelectSubCategory
                        handleChange={handleChange}
                        setSubmitting={setSubmitting}
                        names={navigationList}
                        name="subCategory"
                        values={values}
                      />
                    </label>
                  </li>
                )}
                <li>
                  <label>
                    
                    <MultipleSelectState
                      handleChange={handleChange}
                      setSubmitting={setSubmitting}
                      name="state"
                      placeholder="  "
                    />
                  </label>
                </li>
                <li>
                  <label>
                    <Label label="" />
                    <MultipleSelectColor
                      names={colorProduct}
                      handleChange={handleChange}
                      setSubmitting={setSubmitting}
                    />
                  </label>
                </li>

                <li>
                  <label>
                    <Label label="" />
                    <MultipleSelectSex
                      handleChange={handleChange}
                      setSubmitting={setSubmitting}
                      name="sex"
                      placeholder="   "
                    />
                  </label>
                </li>
                {(values.category === '   ' ||
                  values.category === '') && (
                  <li>
                    <label>
                      <Label label="" />
                      <MultipleSelectSize
                        handleChange={handleChange}
                        setSubmitting={setSubmitting}
                        sizeFootwear={sizeFootwear}
                        sizeClothes={sizeClothes}
                        values={values}
                        name="size"
                      />
                    </label>
                  </li>
                )}
              </SelectorsList>
            </Box>
            <Box>
              <label>
                <Label label="" />
                <Explainment>
                       .  
                    .
                </Explainment>
                <FieldImagesList>
                  {pictures.map((_, index) => (
                    <FieldAddImages
                      key={index}
                      values={values}
                      setFieldValue={setFieldValue}
                    />
                  ))}
                </FieldImagesList>
              </label>
            </Box>
            <Box>
              <label className="checkbox">
                <FieldsCheckboxes
                  title=" "
                  handleChange={handleChange}
                  setSubmitting={setSubmitting}
                  name="eco"
                  text=",      
                    ,       
                          
                    ."
                />
              </label>
            </Box>
            <Box>
              <label className="checkbox">
                <FieldsCheckboxes
                  title=" "
                  handleChange={handleChange}
                  setSubmitting={setSubmitting}
                  name="isUkraine"
                  text=",      ."
                />
              </label>
            </Box>
            {values.category !== '/' && (
              <PriceComponent
                handleChange={handleChange}
                setSubmitting={setSubmitting}
                values={values}
              />
            )}
            <Buttons>
              <Button
                type="button"
                sx={viewProductButton}
                disabled={isSubmitting}
                onClick={aheadViewProduct}
              >
                 
              </Button>
              <Button
                type="submit"
                sx={addProductButton}
                disabled={isSubmitting}
              >
                
              </Button>
            </Buttons>
            {onViewProduct && createPortal(<ViewAheadComponent />, modalEnter)}
          </Form>
        )}
      </Formik>
    </ContainerAddProduct>
  );
}
